import { Fragment, useContext, useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import Cleave from "cleave.js/react"
import {
  Row,
  Col,
  Form,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  CardText
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors } from "@utils"
import Select from "react-select"
import Flatpickr from "react-flatpickr"
import "@styles/react/libs/flatpickr/flatpickr.scss"
import { Indonesian } from "flatpickr/dist/l10n/id.js"
import { useForm, Controller } from "react-hook-form"
import {
  storeName,
  pageTitle,
  postData,
  putData,
  resetSelectedData,
  getDataById
} from "./store"
import { useDispatch, useSelector } from "react-redux"

const FormAddEdit = ({
  formState,
  updateDataToApi,
  postDataToApi,
  handleReset
}) => {
  const { t } = useTranslation()
  const { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { colors } = useContext(ThemeColors)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const {
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({})

  return (
    <Form
      onSubmit={
        formState === "edit"
          ? handleSubmit(updateDataToApi)
          : handleSubmit(postDataToApi)
      }
    >
      <Row className="gy-1 pt-75">
        <Col md={12} xs={12}>
          <Label className="form-label" for="title">
            {t("Title")}
            <span className="text-danger">*</span>
          </Label>
          <Controller
            defaultValue={store.selectedData?.title ?? ""}
            control={control}
            id="title"
            name="title"
            render={({ field }) => (
              <Input
                {...field}
                className={
                  errors.title ? "form-control is-invalid" : "form-control"
                }
                id="title"
                invalid={errors.title && true}
              />
            )}
          />
        </Col>

        <Col md={12} xs={12}>
          <Label className="form-label" for="description">
            {t("Description")}
            <span className="text-danger">*</span>
          </Label>
          <Controller
            defaultValue={store.selectedData?.description ?? ""}
            control={control}
            id="description"
            name="description"
            render={({ field }) => (
              <Input
                {...field}
                className={
                  errors.description
                    ? "form-control is-invalid"
                    : "form-control"
                }
                id="description"
                invalid={errors.description && true}
              />
            )}
          />
        </Col>

        <Col md={12} xs={12}>
          <Label className="form-label" for="subject">
            {t("Subject")}
            <span className="text-danger">*</span>
          </Label>
          <Controller
            defaultValue={store.selectedData?.subject ?? ""}
            control={control}
            id="subject"
            name="subject"
            render={({ field }) => (
              <Input
                {...field}
                className={
                  errors.subject ? "form-control is-invalid" : "form-control"
                }
                id="subject"
                invalid={errors.subject && true}
              />
            )}
          />
        </Col>

        <Col xs={12} className="text-center mt-2 pt-50">
          <Button type="submit" className="me-1" color="primary">
            {t("Submit")}
          </Button>
          <Button type="reset" color="secondary" outline onClick={handleReset}>
            {t("Cancel")}
          </Button>
        </Col>
      </Row>
    </Form>
  )
}

export default FormAddEdit
